.ImageCarousel {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 300px;
    position: relative;
  }
  
  .ImageCarousel-image {
    width: 80%;
    height: 100%;
    object-fit: cover;
  }
  
  .ImageCarousel-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    font-size: 2em;
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
  }
  
  .ImageCarousel-button.left {
    left: 0;
  }
  
  .ImageCarousel-button.right {
    right: 0;
  }
  